<template>
  <div class="hello">
    <!-- <h1>{{ msg }}</h1> -->
    <h2>EmoFace</h2>
    <video v-show="videoflg" ref="video" width="100%" autoplay playsinline></video>
    <canvas v-show="canvasflg" ref="canvas" id="canvas"></canvas>
    <img id="image">
    <button id="snap" v-on:click="capture()">Snap Photo</button>
  </div>
</template>

<script>
export default {
  name: 'hello1',
  data () {
    return {
      video: {},
      canvas: {},
      captures: [],
      videoflg: true,
      canvasflg: false,
      emotionArr: [],
      count: 0
    }
  },
  mounted () {
    this.video = this.$refs.video
    const medias = {audio: false, video: true}
    // if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
    //   navigator.mediaDevices.getUserMedia({ video: true }).then(stream => {
    //     this.video.src = window.URL.createObjectURL(stream)
    //     this.video.play()
    //   })
    // }
    navigator.getUserMedia(medias, this.successCallback, this.errorCallback)
  },
  methods: {
    successCallback (stream) {
      this.video.srcObject = stream
    },
    errorCallback (err) {
      alert(err)
    },
    makeblobdata (dataURL) {
      var BASE64_MARKER = ';base64,'
      var parts
      var contentType
      var raw
      var rawLength
      if (dataURL.indexOf(BASE64_MARKER) === -1) {
        parts = dataURL.split(',')
        contentType = parts[0].split(':')[1]
        raw = decodeURIComponent(parts[1])
        return new Blob([raw], { type: contentType })
      }
      parts = dataURL.split(BASE64_MARKER)
      contentType = parts[0].split(':')[1]
      raw = window.atob(parts[1])
      rawLength = raw.length

      var uInt8Array = new Uint8Array(rawLength)

      for (var i = 0; i < rawLength; ++i) {
        uInt8Array[i] = raw.charCodeAt(i)
      }
      return new Blob([uInt8Array], { type: contentType })
    },
    capture () {
      // alert(navigator.platform)
      this.canvas = this.$refs.canvas
      var w = this.video.offsetWidth
      var h = this.video.offsetHeight
      this.canvas.setAttribute('width', w)
      this.canvas.setAttribute('height', h)
      // var context = this.canvas.getContext('2d').drawImage(this.video, 0, 0, w, h)
      this.$data.canvasflg = true
      this.canvas.getContext('2d').drawImage(this.video, 0, 0, w, h)
      this.$data.videoflg = false
      this.captures.push(this.canvas.toDataURL('image/png'))
      // var imgUrl = this.canvas.toDataURL('image/png')
      var params = { }
      console.log(this.makeblobdata(this.canvas.toDataURL()))
      var that = this
      $.ajax({
        // NOTE: You must use the same location in your REST call as you used to obtain your subscription keys.
        url: 'https://westus.api.cognitive.microsoft.com/emotion/v1.0/recognize?' + $.param(params),
        beforeSend: function (xhrObj) {
          // Request headers, also supports "application/octet-stream"
          xhrObj.setRequestHeader('Content-Type', 'application/octet-stream')
          // NOTE: Replace the "Ocp-Apim-Subscription-Key" value with a valid subscription key.
          xhrObj.setRequestHeader('Ocp-Apim-Subscription-Key', '4dfe28f1041d48e1a36f469e67ec93d6')
        },
        type: 'POST',
        // Request body
        data: this.makeblobdata(this.canvas.toDataURL()),
        processData: false
      }).done(function (data) {
        // Get face rectangle dimensions
        // var faceRectangle = data[0].faceRectangle
        // var faceRectangleList = $('#faceRectangle')
        console.log(data)
        that.$data.emotionArr.push(data[0].scores)
        that.$data.canvasflg = false
        that.$data.videoflg = true
        // // Append to DOM
        // for (var prop in faceRectangle) {
        //   faceRectangleList.append("<li> " + prop + ": " + faceRectangle[prop] + "</li>");
        // }
        // // Get emotion confidence scores
        // var scores = data[0].scores;
        // var scoresList = $('#scores');
        // // Append to DOM
        // for(var prop in scores) {
        //   scoresList.append("<li> " + prop + ": " + scores[prop] + "</li>")
        // }
      }).fail(function (err) {
        alert('Error: ' + JSON.stringify(err))
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



